<template>
  <div id="Note" class="wow fadeIn">
    <img src="../../assets/images/banner-note.jpg" alt=""> 
  <main>
    <div class="title wow fadeInUp"><div class="txt"><span>Note</span>注意事項</div></div>
     <div class="content wow fadeInUp" v-html="about"></div>
  </main>
</div>
</template>

<script>
 import {WOW} from '@/assets/js/util.js'
export default {
  name: 'Note',
  data() {
  	return {
      about: ""
    }
  },
  mounted(){
    this.$get('?cmd=about&id=5').then((res) => {
        this.about = res;
      })
     new WOW({live: false}).init()
   },
  methods: {
  	

  }
}
</script>

<style scoped>
#Note{opacity: 0}

</style>



