<template>
  <div class="Home wow fadeIn">
    <div class="banner">
      <img @load="imageLoaded" src="../../assets/images/banner1.jpg" width="100%" alt="千合"><!--  -->
      <img class="banner-text" data-wow-delay=".3s" src="../../assets/images/banner-text.png" width="100%" alt="千合">
    </div>

    <div class="b_content" id="menuServe">
    <img class="down-up wow fadeInUp" data-wow-delay="1s"  @click="downUp" src="../../assets/images/downUp.png">

      <div class="main">
        <div class="col-sm-6 col-xs-12 pr-45 wow fadeInUp" data-wow-delay="0.5s">
          <div class="col-xs-5">
            <h1 class="white">貨件查詢</h1>
            <h4 class="white">(物流進度簽收查詢)</h4>
          </div>
          <div class="col-xs-7">
              <div style="position: relative;">
                    <input class="search" type="text" v-model="current_number" @keypress.enter="redirect_to_search()" placeholder="輸入提單號碼">
                    <router-link :to="{
                        name: 'Search',
                        params: {
                            number: current_number
                        }
                    }"><input type="submit" value=""></router-link>
                </div>
          </div>
          <div class="clear"></div>
        </div>
        <div class="col-sm-6 col-xs-12 pl-45 wow fadeInUp" data-wow-delay="0.8s">
          <div class="col-xs-5">
            <h1 class="white">服務項目</h1>
          </div>
          <div class="col-xs-7 white">
              <p>兩岸三地進出口報關服務</p>
              <p>海空運進出口貨運承攬業務</p>
              <p>國內南北貨物派送服務</p>
          </div>
          <div class="clear"></div>
        </div>
          <div class="clear"></div>
      </div>
    </div>

  <main>


     <div class="title wow fadeInUp" data-wow-delay="0.1s"><div class="txt"><span>News</span>最新消息</div></div>
     <ul class="news wow fadeInUp" data-wow-delay="0.2s">
       <li v-if="index<=2" v-for="(news,index) in news" :key="index">
         <p class="year">{{news.year}}</p>
         <div class="list">
           <div class="time">
             <h4>{{news.month}}月</h4>
             <h3>{{news.day}}</h3>
           </div>
           <div class="c">
             <router-link :to="{path:'/news',query: {id: news.id}}" >{{news.title}}</router-link>
             <p>{{news.mome}}</p>
           </div>
           <div class="more"><router-link :to="{path:'/news',query: {id: news.id}}" >more ></router-link></div>
         </div>
          <div class="clear"></div>
       </li>
    </ul>


    <div class="col-sm-6 col-xs-12 about wow fadeInUp" data-wow-delay="0.2s" id="menuAbout">
       <div class="title"><div class="txt"><span>About</span>關於我們</div></div>
       <div class="content" v-html="about">
       </div>
    </div>
    <div class="col-sm-6 col-xs-12 broad wow fadeInUp" data-wow-delay="0.3s" id="menuInfo">
       <div class="title"><div class="txt"><span>Broad</span>訊息公告</div></div>
       <div class="content" v-html="broad">
          
       </div>
    </div>
          <div class="clear"></div>
  </main>

  <section class="bgf2">
  <div class="i-content" >
     <div class="i-down wow fadeInUp" data-wow-delay="0.1s" id="menuDownload">
       <h3 class="i-title">表單下載</h3>
       <div class="down-link">
           <p v-for="(download,index) in downloads" :key="index">
            <a  download :href="download.downloadurl">{{download.title}}<img class="icon" src="../../assets/images/down.png" alt="下載"></a>
           </p>
    
       </div>
     </div>

     <div class="i-link wow fadeInUp" data-wow-delay="0.2s"  id="menuLinks">
       <h3 class="i-title">快速連結</h3>
       <div>

         <div class="down-link">
           <p v-for="(link,index) in links" :key="index" v-if="index<=4">
              <a target="_blank" :href="link.link">{{link.myname}}</a>
           </p>
         </div>
         <div class="down-link">
           <p v-for="(link,index) in links" :key="index" v-if="index>=5">
              <a target="_blank" :href="link.link">{{link.myname}}</a>
           </p>
         </div>
       </div>
      <div class="clear"></div>
     </div>
      
     <div class="clear"></div>
   </div>
  </section>
   
      
  </div>
</template>

<script>
import {WOW} from '@/assets/js/util.js'

export default {
  name: 'Home',

  data() {
  	return {
        current_number: null,
      about: "",
      broad: "",
      news:[],
      downloads:[],
      links:[]
    }
  },
  mounted(){

        this.init();
  },
    watch:{
        '$route':{
            handler(to,from){
                 var section_id = to.query.menu;
                 if(section_id){
                   this.selectMenu(section_id);
                 }
            },
            immediate: true
        }
    },
  methods: {
      async init() {
          await this.$get('?cmd=about&id=1').then((res) => {
              this.about = res;
          })
          await this.$get('?cmd=about&id=2').then((res) => {
              this.broad = res;
          })
          await this.$get('?cmd=news').then((res) => {
              this.news = res;
          })
          await this.$get('?cmd=downloads').then((res) => {
              this.downloads = res;
          })
          await this.$get('?cmd=links').then((res) => {
              this.links = res;
          })
          await new Promise(resolve => {
             // $(".Home").addClass(" wow fadeIn cur");
              new WOW({live: false}).init()
              resolve();
          });
          await new Promise(resolve => {
              var section_id = this.$route.query.menu;
              if(section_id){
                  this.selectMenu(section_id);
              }
              resolve();
          });
      },
        redirect_to_search() {
            this.$router.push(
                {
                    name: 'Search',
                    params: {
                        number: this.current_number
                    }
                }
            ); 
        },
        selectMenu(section_id) { //锚点滚动到固定位置
            this.$nextTick(() => {
                var anchorElement = document.getElementById(section_id);
                if(anchorElement){
                  $('html,body').animate({ scrollTop: anchorElement.offsetTop - 130}, 400);

                  this.bus.$emit('closeMenu',false);//改变app.vue的menuShow=false 
                }
            })
        },
      imageLoaded() { //某圖片加載完
          $(".banner-text").addClass("wow fadeInUp"); 
       },
      downUp() { 
          $('html,body').animate({ scrollTop: $("main").offset().top-130}, 800);
      },

  }

}
</script>

<style scoped>
 .Home{opacity: 0; }

.banner{position: relative; line-height: 0}
.banner-text{ width: 40%; position: absolute; top: 36%; left: 50%; margin: 0px 0 0 -20%; }
.down-up{position: absolute; width: 60px;left: 50%;margin:-120px 0 0 -30px;cursor: pointer; animation-name:container; animation-duration: 1s;  animation-iteration-count: infinite;}
@keyframes container{
  0% {
  transform: translate3d(0,0px,0); /*设置只在Z轴上移动*/
  }
  50%{
  transform: translate3d(0,10px,0);
  }
  100%{
  transform: translate3d(0,0px,0);
  }
}

.b_content{ position: relative;height: 200px; margin-top: -200px; z-index: 2; background: rgba(0,0,0,.3); padding: 60px 0 30px 0;}
.main{position: relative; max-width: 930px; margin: 0 auto;}
.main:after{content: ""; position: absolute; left: 50%;top: 0; height: 100px; width: 2px; background: #fff;}
.main>.col-sm-6.pr-45{padding-right:45px; }
.main>.col-sm-6.pl-45{padding-left:45px; }
.main h1{letter-spacing: 2px;line-height: 1;}

.b-search{position: relative;}
input.search{border-radius: 3px;font-size: 16px;border:none; padding: 10px 50px 10px 10px; width: 100%}
input[type=submit]{position: absolute; right: 10px; top: 7px; width: 28px; height: 28px;border:solid 0px #003398; cursor: pointer; background: url("../../assets/images/go.png"); background-size: 100%;  }
.about{padding-right: 20px;}
.broad{padding-left: 20px;}
.about .content,.broad .content{padding:0 15px 25px 15px ;}

.i-content{ max-width: 970px;  margin: 0 auto; }
.bgf2{ background: #0066cb; padding: 30px 0 50px 0 }
.i-title{color: #fff;margin-bottom:10px;}
.i-down{ width: 30%;float: left; }
.i-link{ width: 65%; float: right; }
.i-link .down-link{width: 49%;  float: left;}
.i-link .down-link:last-child{margin-left: 2%;}
.down-link{ border-radius: 10px; overflow: hidden;}
.down-link a{position: relative; background: #fff; border-bottom: solid 1px #f2f2f2; padding: 12px 30px;font-weight: 800; display: block; font-size: 18px;}
.down-link a img.icon{position: absolute; width:25px; right: 20px; top: 50%; margin-top:-10px;  }
.down-link a:hover{ background: #cad3e7;}

@media only screen and (max-width: 1400px){
  .b_content{padding: 30px 0 0 0;margin-top: -140px; height: 140px}
.down-up{ display: none;}
}
@media only screen and (max-width: 767px){

.b_content{height: auto; margin-top: 0px; padding: 0px; background:none;}
.main:after{width: 0px; }
.main h1{font-size: 30px; }
.main h4{font-size: 15px;}
.main>.col-sm-6.pr-45{padding:20px; background: #333333; }
.main>.col-sm-6.pl-45{padding:20px;position: relative; }
.main>.col-sm-6.pl-45:after{content: ""; position: absolute; left: 5%;bottom: 0; height: 1px; width: 90%; background: #f0f0f0; }
.main>.col-sm-6.pl-45 .white{color: #333; }
.about{padding-right: 0px;}
.broad{padding-left: 0px;}
.banner-text,.down-up{display: none}

.i-content{ padding: 0 15px; }
.i-down,.i-link{ width: 100%;margin:20px 0 0 0; }
.i-link .down-link{width: 100%;  float: none;}
.i-link .down-link:last-child{margin: 10px 0 0 0%;}

}



</style>



