<template>
  <div id="Contact" class="wow fadeIn">
     <iframe class="map"  src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3613.154820253454!2d121.2520471150076!3d25.096620033942983!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x18a75603c9128cc2!2z5Y6a5oGp5pCs5a626LKo6YGL5pyJ6ZmQ5YWs5Y-4!5e0!3m2!1szh-TW!2stw!4v1545710327351" frameborder="0" style="border:0" allowfullscreen></iframe>

  <main>
    <div class="title wow fadeInUp"><div class="txt"><span>Contact</span>聯絡我們</div></div>
     
     <div class="contact wow fadeInUp">
       <div class="col-sm-6 pr-45">
       
           <div class="input"><span>聯絡事項：</span>
             <select v-model="contactInfo.classid" name="classid">
             <option value="業務相關問題">業務相關問題</option>
             <option value="需要專人服務">需要專人服務</option>
             <option value="客訴及建議">客訴及建議</option>
             </select>
           </div>
           <div class="input"><span>姓 &nbsp; &nbsp;名：</span><input v-model="contactInfo.myname" name="myname" type="text" /></div>
           <div class="input"><span>聯絡電話：</span><input v-model="contactInfo.tel" name="tel" type="text" /></div>
           <div class="input"><span>行動電話：</span><input v-model="contactInfo.mobile" name="mobile" type="text" /></div>
           <div class="input"><span>E-mail：</span><input v-model="contactInfo.email" name="email" type="text" /></div>
           <div class="input"><span>通訊地址：</span><input v-model="contactInfo.address" name="address" type="text" /></div>
           <div class="input"><span>備註內容：</span><input v-model="contactInfo.mycontent" name="mycontent" type="text" /></div>
           <div class="input"><input @click="submit" type="submit"  value="送 出" /></div>

       </div>
       <div class="col-sm-6 pl-45" v-html="about">
          
       </div>
     <div class="clear"></div>
     </div>
    

  </main>
</div>
</template>

<script>
 import {WOW} from '@/assets/js/util.js'
export default {
  name: 'Contact',
  data() {
  	return {
        about: "",
        contactInfo: {
            test:"",                  //前後2個接收不到 所有加前後2個沒用的字段  奇怪！！
            classid:"業務相關問題",
            myname:"",
            tel:"",
            mobile:"",
            email:"",
            address:"",
            mycontent:"",
            test2:""
        }
    }
  },
  mounted(){
    this.$get('?cmd=about&id=3').then((res) => {
        this.about = res;
        new WOW({live: false}).init()
     })
 },
  methods: {
      submit: function() {
         $("input[type=submit]").attr("disabled",true);
         let _this=this;
         this.$post("?cmd=contact",_this.$qs.stringify(_this.contactInfo)).then(res => {
              if(res.statu==2){
                alert("發訊完成");
                $("input[type=text]").val("");
              }else{
                alert(res.statu);                  
              }
              $("input[type=submit]").attr("disabled",false);
          }, err => {
              alert("請稍後再試！")
              $("input[type=submit]").attr("disabled",false);
          })
      }
  
  }
}
</script>

<style scoped>
#Contact{opacity: 0}
.map{
  width: 100%; height: 400px;
}

.contact{position: relative;}
.contact:after{content: ""; position: absolute; left: 50%;top: 0; height: 100%; width: 1px; background: #999;}

.contact .pr-45{padding-right: 45px;}
.contact .pl-45{padding-left: 45px;}

@media only screen and (max-width: 767px){
.map{height: 300px;}
.contact:after{width: 0px; }
.contact .pr-45{padding-right: 0px; border:solid 1px #ccc; border-radius: 3px;}
.contact .pl-45{padding: 20px 0 0 0;}

}




</style>



