<template>
  <div id="News">
         <img src="../../assets/images/banner-news.jpg" alt="news"> 
     
  <main>
    <div class="title wow fadeInUp"><div class="txt"><span>News</span>最新消息</div></div>
     <ul class="news wow fadeInUp" data-wow-delay="0.1s">
       <li v-for="(list,index) in lists" :key="index">
         <p class="year">{{list.year}}</p>
         <div class="list">
           <div class="time">
             <h4>{{list.month}}月</h4>
             <h3>{{list.day}}</h3>
           </div>
           <div class="c">
             <a href="">{{list.title}}</a>
             <p>{{list.mome}}</p>
           </div>
           <div class="more"><a  @click="viewNew(list.id)">more ></a></div>
         </div>
          <div class="clear"></div>
       </li>

    </ul>

 <v-pagination :total="total" :display="display" :current-page='current' @pagechange="pagechange"></v-pagination>
 

<div  v-if="showNew" class="img-view" >
    <div class="img-layer"></div>
    <div class="img">
     <div class="close" @click="closeNew"></div>
     <ul class="news">
       <li>
         <p class="year" v-text="year"></p>
         <div class="list">
           <div class="time">
             <h4><span v-text="month"></span>月</h4>
             <h3 v-text="day"></h3>
           </div>
           <div class="c">
             <h3 v-text="title"></h3>
           </div>
         </div>
          <div class="clear"></div>
       </li>
     </ul>
           <div class="left"><img v-if="preid>0" @click="viewNew(preid)" src="../../assets/images/left.png" width="22px"></div> 
           <div class="right"><img  v-if="nextid>0" @click="viewNew(nextid)" src="../../assets/images/right.png" width="22px"></div> 
      <div class="content" v-html="mycontent"></div>
    </div>
 </div>

  </main>
</div>
</template>
 <script>
 import {WOW} from '@/assets/js/util.js'
import pagination from '@/components/components/page'
export default {
  name: 'News',
  data() {
  	return {
    	lists: [],
      preid:'1',
      nextid:'1',
      showNew:false,
      year:"",
      month:"",
      day:"",
      title:"",
      mycontent:"",

      total: 0,     // 记录总条数
      display: 6,   // 每页显示条数
      current: 1,   // 当前的页数      

    }
  },
 
  mounted(){
    this.$get('?cmd=news&pageCurrent='+this.current+'&pageSize='+this.display).then((res) => {
        this.lists = res;
        this.total=parseInt(res[0]["totals"]);
     })
      if(this.$route.query.id){
          this.showNew = true;
          this.viewNew(this.$route.query.id)
      }   
      new WOW({live: false}).init()
               
  },
               

  methods: {
      viewNew(id){
          $('html,body').animate({ scrollTop: 0}, 800);
          this.showNew = true;
           this.$get('?cmd=news&id='+id)
          .then((res) => {
               this.preid = res["preid"];
               this.nextid = res["nextid"];
              this.year = res["year"];
              this.month = res["month"];
              this.day = res["day"];
              this.title = res["title"];
              this.mycontent = res["mycontent"];
           })

      },  	
      closeNew(){
          this.showNew = false;
      },

      pagechange:function(currentPage){
         this.$get('?cmd=news&pageCurrent='+currentPage+'&pageSize='+this.display).then((res) => {
            //console.log(res);
            this.lists = res;
            this.total=parseInt(res[0]["totals"]);
         })
      }

  },
   components: {
      'v-pagination': pagination,
    }
}
</script>

<style scoped>

  .img-layer {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
.img-view .img{position: absolute;top:50px; width:50%;  left: 50%; margin: 0 0 0 -25%; padding: 10px 20px; border-radius: 10px; z-index: 1000; background: #fff;}
.img-view .img .close{position: absolute; z-index: 5; right: 10px; top: 10px; cursor: pointer; width: 50px; height: 50px; border-radius: 10px; background: #0066cb;}
.img-view .img .close:before,.img-view .img .close:after{position: absolute; height: 40px; width: 1px; top: 5px; left: 25px; transform: rotate(45deg);  background: #fff; content: "";}
.img-view .img .close:after{transform: rotate(-45deg);}
.more{ cursor:pointer}

.img .news li { padding: 0;}
.img .news li:hover {background: none;}
.img .news li .list .c {
    margin: 0px 60px 0px 95px;padding-top: 20px;
}
.img .news li:before{bottom: -2px; height: 2px;background: #0066cb;    left: 0%;width: 100%;}

.content{position: relative;    display: block; min-height: 300px; overflow-y: auto; margin: 0 80px 50px 80px;}
.left,.right{position: absolute; top: 190px; left: 30px; cursor: pointer;}
.right{left:auto; right: 30px;}


@media only screen and (max-width: 767px){
.img-view .img{  top:10px;width:96%; left: 2%; margin: 0;}
.img .news li .list .c {
    margin: 0px 40px 0px 65px; padding-top: 0px;
}
.img .news li .list .c h3{
   font-size: 15px
}
.content{ margin: 0 15px 50px 20px;}
.left,.right{left: 10px;}
.right{left:auto; right: 10px;}

}
</style>



