<template>
  <div id="app">
    <header class="text-center">
    <router-link :to="'/home'" @click.native="menuShow=false"><img class="logo" src="./assets/images/logo.png"></router-link>
    <div class="menu">
      <div class="btn-menu" :class="{'cur':menuShow===true}" @click="toggleMenu()">
      <span></span><span></span><span></span>
      選單
      </div>	
    </div> 
    </header>
    <nav class="nav" :class="{'cur':menuShow===true}">
        <div class="navmain">
            <div class="nav-menu">
                <router-link :to="'/home'" @click.native="toggleMenu()"><img src="./assets/images/home.png" alt="首页"><p>首页</p></router-link>
                <router-link :to="'/import'" @click.native="toggleMenu()"><img src="./assets/images/excel.png" alt="代理簽收匯入"><p>代理簽收匯入</p></router-link>
                <router-link :to="'/search'" @click.native="toggleMenu()"><img src="./assets/images/search.png" alt="貨件查詢"><p>貨件查詢</p></router-link>
                <router-link :to="'/import'" @click.native="toggleMenu()"><img src="./assets/images/user.png" alt="客戶專區"><p>客戶專區</p></router-link>
                <router-link :to="'/contact'" @click.native="toggleMenu()"><img src="./assets/images/contact.png" alt="聯絡我們"><p>聯絡我們</p></router-link>
                <router-link :to="'/report'" @click.native="toggleMenu()"><img src="./assets/images/house.png" alt="清關報告"><p>清關報告</p></router-link>
                <router-link :to="'/news'" @click.native="toggleMenu()"><img src="./assets/images/news.png" alt="最新消息"><p>最新消息</p></router-link>
                <router-link :to="'/lost'" @click.native="toggleMenu()"><img src="./assets/images/lost.png" alt="失物招領"><p>失物招領</p></router-link>
                <router-link :to="{name:'Home',query:{menu:'menuServe'}}"><img src="./assets/images/serve.png" alt="服務項目"><p>服務項目</p></router-link>
                <router-link :to="{name:'Home',query:{menu:'menuAbout'}}"><img src="./assets/images/about.png" alt="關於我們"><p>關於我們</p></router-link>
                <router-link :to="{name:'Home',query:{menu:'menuInfo'}}"><img src="./assets/images/info.png" alt="訊息中心"><p>訊息中心</p></router-link>
                <router-link :to="{name:'Home',query:{menu:'menuDownload'}}"><img src="./assets/images/download.png" alt="表單下載"><p>表單下載</p></router-link>
                <router-link :to="{name:'Home',query:{menu:'menuLinks'}}"><img src="./assets/images/links.png" alt="快速鏈結"><p>快速鏈結</p></router-link>
                <router-link :to="'/note'" @click.native="toggleMenu()"><img src="./assets/images/warning.png" alt="注意事項"><p>注意事項</p></router-link>
             <div class="clear"></div>
            </div>
        </div>
    </nav>

    <router-view/>
    
    <footer>
    	<main>
        <div class="toTop"><img @click="goTop" src="./assets/images/toTop.png" width="30px"></div>
    	    <div class="col-sm-5 wow fadeInUp">
	    		<div class="foot-logo">
	    			<h2>{{seo.cname}}</h2>
	                <h4>{{seo.ename}}</h4>
	    		</div>
    		</div>
    	    <div class="col-sm-7 wow fadeInUp">
    	         <p>服務中心：{{seo.address}}</p>
    	         <p>客服專線：{{seo.tel}}</p>
    	         <p>傳真號碼：{{seo.fax}}</p>
    	         <p>E-mail客服信箱：{{seo.email}}</p>
     		</div>
     		<div class="clear"></div>
   	    </main>
    </footer>
    <!-- 線上客服 -->
    <div id="Customer">   
     <div class="Customer transition">
       <div class="Customer-t">
         <img src="./assets/images/kfimg.png" alt="">
       </div>
       <div class="Customer-c">
            <a class="popUp" @click="setClassid(1)" ><img src="./assets/images/skype.png" alt="skype"><span>線上詢價</span></a>
            <a class="popUp" @click="setClassid(2)" ><img src="./assets/images/qq.png" alt="qq"><span>線上客服</span></a>
            <a class="popUp" @click="setClassid(3)" ><img src="./assets/images/wx.png" alt="微信"><span>微信客服</span></a>
       </div>
     </div>
    </div>
     <div class="popcon" v-if="customerShow">
      <div class="p-main p-skype">
              
          <div class="p-head">
          <h1 class="title"> 
              <span v-if="classid==1">skype 線上詢價</span> 
              <span v-if="classid==2">QQ線上客服</span>
              <span v-if="classid==3">微信線上客服</span>
          </h1>
            <span @click="customerClose" class="close transition"></span>
         </div>  
         <div class="p-content">
              <div class="down-link">

              <a v-for="cus in customer" :key="cus.id" v-if="cus.classid==1&&classid==1" :href="'skype:'+cus.link+'?chat'" target="_blank"> <img class="skype-icon" src="./assets/images/skype.png" >{{cus.myname}}</a>

              <a v-for="cus in customer" :key="cus.id" v-if="cus.classid==2&&classid==2" :href="'http://wpa.qq.com/msgrd?v=3&amp;uin='+cus.link+'&amp;site=qq&amp;menu=yes'" target="_blank"> <img class="skype-icon" src="./assets/images/qq.png" >{{cus.myname}}</a>

              <a v-for="cus in customer" :key="cus.id" v-if="cus.classid==3&&classid==3" :href="'http://wpa.qq.com/msgrd?v=3&amp;uin='+cus.link+'&amp;site=qq&amp;menu=yes'" target="_blank"> <img class="wx-icon" :src="cus.image" ></a>

             </div>
            <div class="clear"></div>
         </div>             
 
      </div>  
    </div>


  </div>
</template>

<script>
export default {
  name: 'App',
    data() {
	        return {
	          menuShow: false,
              customerShow:false,
              seo:[],
              customer:[],
              classid:1
	        };
        },
    created(){
    	 
    },
    mounted(){
        this.init();
        this.bus.$on("closeMenu", msg => {//home.vue 传值
          this.menuShow = msg;
        });

    },
    methods: { 
        async init() {
            await this.$get('?cmd=seo').then((res) => {
                this.seo = res;
            })
            await this.$get('?cmd=customer').then((res) => {
                this.customer = res;
                console.log(res);
            })
        },
  	    toggleMenu() {
  	        this.menuShow = !this.menuShow
  	    },
        goTop(){
            $('html,body').animate({ scrollTop: 0}, 800);
        },
        customerClose(){
           this.customerShow = false
        },
        setClassid(i){
            this.customerShow = true;
            this.classid = i
        }
    }        
}
</script>

<style scoped>
#app {
      
}
#Customer{position:fixed; z-index:99;right: 0px; top:40%;    }
.Customer{ margin-right: -80px; -webkit-transition: all 0.3s linear;
    -moz-transition: all 0.3s linear;
    -o-transition: all 0.3s linear;
    transition: all 0.3s linear;}
.Customer-t{float: left; width: 30px; height: 90px; box-shadow: 0 0 10px #333; padding-top:7px;  cursor: pointer; background: #036eb7; text-align: center; border-radius: 10px 0 0 10px; }
.Customer-c{float: left; width: 80px;   padding: 5px; background: #fff; }
.Customer-c a{display: block; text-align: center; cursor: pointer; line-height: 1.2}
.Customer-c a{padding: 5px 0;margin-bottom: 5px; border-bottom: solid 1px #ccc;}
.Customer-c a:last-child{border-bottom: none;}
.Customer-c a img{ width: 41px; }
.Customer-c a span{ display: block; color: #111;font-size: 14px }
.Customer-c a:hover{ opacity: 0.7 }
.Customer:hover{ margin-right: 0px; }
.Customer:hover .Customer-c{box-shadow: 0 0 10px #333;}
@media only screen and (max-width: 767px){
#Customer{display: none;}
}
.popcon{ display:  ; position: fixed; width: 100%; height: 100%;background: rgba(0,0,0,.5); top: 0; left: 0; z-index: 9999 }
.popcon .close{position: absolute;right: 15px;top: 10px;cursor: pointer; display: inline-block;width: 40px; height: 40px; -webkit-transition: all 0.3s linear;-moz-transition: all 0.3s linear;-o-transition: all 0.3s linear; transition: all 0.3s linear;}
.popcon .close:after,.popcon .close:before{ content: ""; position: absolute; top: 30px;border: solid 1px #004796;width: 40px; transform: rotate(45deg); -ms-transform: rotate(45deg);-moz-transform: rotate(45deg);}
.popcon .close:after {transform: rotate(-45deg);-ms-transform: rotate(-45deg);-moz-transform: rotate(-45deg);}
.popcon .close:hover:after,.popcon .close:hover:before{border: solid 1px #333;}
.popcon .p-main{ display: inline-block; left:50%; top: 40%;margin-left: -340px;margin-top: -200px; background: #fff; position: relative; padding: 20px 45px 50px 45px; border-radius: 20px; }

@media only screen and (max-width: 767px){
.popcon .p-main{ width: 90%; left:5%; top: 10%; margin-left: 0px;margin-top: 0px;padding: 20px 15px 20px 15px; }
}
.p-content .down-link{ padding:0; }
.p-skype .p-content{ font-size: 21px; padding: 40px 150px 20px 150px; line-height: 2.1;    }
.p-skype .p-content .skype-icon{margin:0 15px 0px 0;}
.p-skype .p-content .wx-icon{max-width: 288px;}
.p-skype .p-content .down-link{ width: 290px; border: solid 1px #f2f2f2; }
.p-skype .p-content .down-link a{ padding: 10px 20px 10px 20px;display: flex;    border-bottom: solid 1px #f2f2f2; }
.p-skype .down-link a:last-child {
    border-bottom: none;
}
.p-skype .p-content .down-link a:hover{background:#f5f5f5 }
@media only screen and (max-width: 767px){
.p-skype{margin-left: 0px;margin-top: 0px}
.p-skype .title{ font-size: 28px }
.p-skype .p-content{ padding: 40px 0px 20px 0px; }
}
</style>



