<template>
  <div id="Lost">
          <img src="../../assets/images/banner-search.jpg" alt=""> 
    
  <main>
    <div class="title wow fadeInUp"><div class="txt"><span>Lost and found</span>失物招領</div></div>
        <div class="input wow fadeInUp">
            <select @change="changeDate" id="pDate">
             <option value="">開始日期</option>
            </select>
            ~
            <select @change="changeDate" id="nDate">
             <option value="">結束日期</option>
              <option v-for="(list,index) in date" :key="index" :value="list.mytime">{{list.mytime}}</option>
            </select>
        </div>

        <ul class="lost">
          <li v-for="(list,index) in lists" :key="index"  class="wow fadeInUp"  data-wow-delay="0.1s">
            <img @click="clickImg($event)" :src="list.image" :alt="list.myname" :data-time="list.mytime" width="100%">
            <p class="time">{{list.mytime}}</p>
            <p class="t">{{list.myname}}</p>
          </li>
         
        </ul>
        <div class="clear"></div>

         <div v-if="showMore" class="text-center">
          <img class="more" @click="viewMore" src="../../assets/images/more.png" alt="more">
        </div>  
         

 <div v-if="showImg" class="img-view wow fadeInUp" @click="viewImg">
    <div class="img-layer"></div>
    <div class="img">
      <img :src="imgSrc">
      <p>{{mytime}}</p>
      <p><b>{{myname}}</b></p>
    </div>
 </div>

  </main>
</div>
</template>

<script>
 import {WOW} from '@/assets/js/util.js'
export default {
  name: 'Lost',
  data() {
  	return {
      showImg:false,
      showMore:false,
    	imgSrc: '',
      mytime:'',
      myname:'',
      date:[],
      lists:[],
     // total: 0,     // 记录总条数
      display: 12,   // 每页显示条数
      current: 1,   // 当前的页数      
    }
  },
  mounted(){
    this.$get('?cmd=lost&type=date&pageCurrent='+this.current+'&pageSize='+this.display).then((res) => {
        this.lists = res;
        //this.total=parseInt(res[0]["totals"]);
        var strDate=res[0]["allDate"].split(","); //字符分割 
        for (var i=1;i<strDate.length ;i++ ) 
        {  
          var strDate2=strDate[i].split(" ");
          $("#pDate,#nDate").append("<option value="+strDate2[0]+">"+strDate2[0]+"</option>");
        } 

        //console.log(res);
        if(res.length<=this.display){
            this.current++;
            this.showMore=true;
        }
        // if(this.display<this.total){
        //   this.showMore=true;
        // }
    }) 
    new WOW({live: false}).init()
  },
  methods: {
      clickImg(e) {
          this.showImg = true;
          this.imgSrc = e.currentTarget.src;
          this.mytime = e.currentTarget.dataset.time;
          this.myname = e.currentTarget.alt;
      },
      viewImg(){
          this.showImg = false;
      },
      changeDate() {
          this.current=1;
       // alert($("#pDate").val()+"--"+$("#nDate").val());
        this.$get('?cmd=lost&pageCurrent='+this.current+'&pageSize='+this.display+'&pdate='+$("#pDate").val()+'&ndate='+$("#nDate").val()).then((res) => {
          this.current++;
          this.lists = res; 
          //console.log(res);
          if(res.length<this.display){
            this.showMore=false;
          }else{
            this.showMore=true;
          }

          // this.total=parseInt(res[0]["totals"]);
          // if(this.display<this.total){
          //   this.showMore=true;
          // }else{
          //   this.showMore=false;
          // }
        }) 
      },
      viewMore(){
        this.$get('?cmd=lost&pageCurrent='+this.current+'&pageSize='+this.display+'&pdate='+$("#pDate").val()+'&ndate='+$("#nDate").val()).then((res) => {
          // for (let i = 0; i <= res.length - 1; i++) {
          //   this.lists.push(res[i])
          // }
          this.current++;
          this.lists=this.lists.concat(res);
          if(res.length<this.display){
            this.showMore=false;
          }
          // var display=this.current*this.display;
          // if(display<this.total){
          //   this.current++;
          //   this.showMore=true;
          // }else{
          //   this.showMore=false;
          // }
        }) 
       },
  	
  }
}
</script>

<style scoped>

.input{max-width: 300px;}
.lost{margin:0 -1%;}
.lost li{width: 23%;margin:0 1% 30px 1%;border:solid 1px #0066cb;border-radius: 10px; padding: 8px; float: left; }
.lost li img{cursor: pointer;}

  .img-layer {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
.img-view .img{position: fixed;top:50px; width:50%; max-height: 90%; overflow-y: scroll; left: 50%; margin: 0 0 0 -25%; padding: 10px; border-radius: 10px; z-index: 1000; background: #fff;}
.img-view .img img{width: 100%}
.more{width: 55px; cursor:pointer}

@media only screen and (max-width: 767px){
.lost{margin:0 -1%;}
.lost li{width: 48%;}
.img-view .img{ width:96%; left: 2%; margin: 0;}

}

</style>



