<template>
  <div id="Report" class="wow fadeIn">
         <img src="../../assets/images/banner-report.jpg" alt=""> 

  <main>
    <div class="title wow fadeInUp"><div class="txt"><span>Report</span>清關報告</div></div>

      <div class="report-signin wow fadeInUp">
         <div class="col-sm-6">
           <div class="signin">
               <h2>Sign in 登入</h2>
              <div class="input"><span>代理編號：</span><input name="uid" type="text" /></div>
              <div class="input"><span>通行密碼：</span><input name="password" type="password" /></div>
              <div class="input"><input type="submit" value="確 認" /></div>
            </div>
         </div>
         <div class="col-sm-6">
           <div class="note">
              <p class="note-t">Note 注意</p>
              <div v-html="about">
              </div>
            </div>
          </div>
          <div class="clear"></div>
     </div>

 
       <div class="report  wow fadeInUp" v-if="isLogin">
           <h1 class="t">發貨公司：吖吖公司</h1>
           <div class="c">
               <div class="col-sm-6 pr-45">
                   <div class="input">
                   <span>清關日期：</span>
                    <select>
                      <option>2018-05-05</option>
                      <option>2018-05-05</option>
                    </select>
                    ~
                    <select>
                      <option>2018-05-05</option>
                      <option>2018-05-05</option>
                    </select>
                   </div>
                   <div class="input"><span>條碼袋號：</span><input name="myname" type="text" /></div>
                   <div class="input"><span>通關狀態：</span>
                      <select>
                        <option>2018-05-05</option>
                        <option>2018-05-05</option>
                      </select>
                   </div>
               </div>
               <div class="col-sm-6 pl-45">
                   <div class="input"><span>收件公司：</span><input name="myname" type="text" /></div>
                   <div class="input"><span>備 &nbsp; &nbsp;註：</span><input name="myname" type="text" /></div>
               </div>
               <div class="clear"></div>
           </div>
           <div class="table-line">
           <table>
             <thead>
               <th>清關日期</th>
               <th>條碼袋號</th>
               <th>件數</th>
               <th>重量</th>
               <th>掃描時間</th>
               <th>進口稅</th>
               <th>狀態</th>
               <th>備註</th>
             </thead>
             <tbody>
               <tr>
                 <td>2018-05-05</td>
                 <td>56445X235</td>
                 <td>12</td>
                 <td>12</td>
                 <td>2018-05-05 04:12</td>
                 <td>11</td>
                 <td>放行</td>
                 <td>進口稅備註</td>
               </tr>
               <tr>
                 <td>2018-05-05</td>
                 <td>56445X235</td>
                 <td>12</td>
                 <td>12</td>
                 <td>2018-05-05 04:12</td>
                 <td>11</td>
                 <td>放行</td>
                 <td>進口稅備註</td>
               </tr>
             </tbody>
           </table>
          </div>
           
           <div class="col-sm-7">
              <div class="totle">
              <div class="item">
                <p>總筆數：352</p>
                <p>總件數：352</p>
              </div>
              <div class="item">
                <p>放行筆數：352</p>
                <p>未放行件數：2</p>
              </div>
              <div class="item">
                <p>總重量：35,235.1</p>
                <p>進口稅金：35,223</p>
              </div>
              </div>
           </div>
           <div class="col-sm-5 text-right page">
             <v-pagination :total="total" :display="display" :current-page='current' @pagechange="pagechange"></v-pagination>
           </div>
           <div class="clear"></div>

       </div>

  </main>
</div>
</template>

<script>
import {WOW} from '@/assets/js/util.js'
import pagination from '@/components/components/page'
export default {
  name: 'Report',
  data() {
  	return {
    	about:"",
      isLogin:false,//是否登入

      total: 0,     // 记录总条数
      display: 6,   // 每页显示条数
      current: 1,   // 当前的页数      
    }
  },
  mounted(){
    this.$get('?cmd=about&id=4').then((res) => {
        this.about = res;
      })
    new WOW({live: false}).init()
   },
  methods: {
  	

  },
   components: {
      'v-pagination': pagination,
    }
}
</script>

<style scoped>
#Report{opacity: 0}
.report-signin{margin: 40px 0;}
.signin{margin-right:30px; border:solid 1px #b1b1b1; border-radius: 5px; padding:30px 30px 10px 30px;}
.signin .input input[type=text]{ border-bottom: solid 1px #333;}
.signin h2{color: #0066cb; }
.note{margin-left: 30px;}
.note-t{background: #cd4643; border-radius: 5px; color: #fff; display: inline-block; padding: 5px 20px; margin-bottom: 20px; font-size: 32px;}

h1.t{background: #283944;text-align: center; color: #fff; padding:5px;}
.report{border:solid 1px #283944;}
.input input[type=text]{border-bottom: solid 1px #ccc;}
select{border: solid 1px #ccc;}
.report .c{padding:20px 50px;}
.report .pr-45{padding-right: 45px;}
.report .pl-45{padding-left: 45px;}

table{width: 100%;text-align: center;    border-spacing: 0;  border-collapse: collapse; }
table thead th{position: relative; border-top:solid #333 1px; border-bottom:solid #333 1px; padding:5px; background: #f0f0f0;}
table thead th:after{ position: absolute; width: 1px; height: 60%; right: -1px; top: 20%; background: #333; content: "";z-index: 2;}
table thead th:last-child:after{width: 0px;}
table tr td{ border-bottom: 1px solid #ddd;border-right: 1px solid #ddd; padding:5px;}
table tr td:last-child{ border-right: 0px solid #ddd; }

.table-line{padding-top: 5px; border-top: solid 1px #333; }
.totle{display: flex; color: #cd4643; font-size: 18px;line-height: 1.6; padding:20px;}
.totle .item{ padding-left: 30px; }
.page{padding: 20px;}

@media only screen and (max-width: 767px){
.signin{margin-right:0px; padding:20px 20px 0px 20px;}
.note{margin:20px 0px 0 0;}

h1.t{text-align: left; font-size: 18px;}
.report .c{padding:0px 0px;}
.report .pr-45{padding-right: 0px;}
.report .pl-45{padding-left: 0px;}
.input span { width: 95px; font-size: 15px; letter-spacing: 0px;}
select{padding:0px;}
.table-line{overflow-x: scroll;}
table thead th,table tr td{ word-break: keep-all;white-space:nowrap;  }
.totle{padding:20px 0;}
.totle .item{ padding-left: 10px; }


}

</style>



